.about-bg {
  background-color: #fff5f6;
}

.caption {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &__title {
    margin-bottom: 15px;
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    text-transform: uppercase;
    color: $secondary-color;
  }
}

.slogan {
  &__title {
    margin-bottom: 30px;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 500;
    color: $main-black-color;
  }

  &__text {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 500;
    color: #907e82;
  }

  &__indent {
    margin-top: 20px;
  }

