// mobile
@media screen and (max-width: 767px) {
  .caption {
    padding-top: 40px;

    &__title {
      line-height: 21px;
    }
    &__subtitle {
      font-family: 'Titan One';
      font-style: normal;
      font-weight: 400;
      font-size: 34px;
      line-height: 39px;
      text-align: center;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: $accent-color;
    }
  }
  .about__img-cow {
    max-width: 100%;
    margin-bottom: 40px;
  }

  .slogan {
    position: relative;
    width: calc(80% - 20px);

    &__title {
      font-size: 12px;
      line-height: 19px;
      margin-bottom: 40px;
    }

    &__text {
      font-size: 12px;
      line-height: 19px;
    }
    &::after {
      position: absolute;
      content: url(./../images/about/milk-bg.png);
      top: 75px;
      left: -15px;
    }
  }

  .about__btn--block {
    margin-top: 20px;
  }

  .about {
    &__list {
      display: flex;
      justify-content: center;
      flex-direction: column;
      margin-top: 40px;
    }

    &__item {
      width: calc(80% - 20px);
      padding-bottom: 32px;
    }

    &__icon {
      width: 50px;
      height: 50px;
      margin-bottom: 10px;
    }

    &__title {
      font-family: 'Titan One';
      font-style: normal;
      font-weight: 400;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: $accent-color;
      font-size: 36px;
      line-height: 41px;
      margin-bottom: 20px;
    }

    &__text {
      font-family: 'DM Sans';
      font-style: normal;
      font-weight: 700;
      font-size: 12px;
      line-height: 170%;
      color: $main-black-color;
    }
  }
}

// tablet
@media screen and (min-width: 768px) and (max-width: 1200px) {
  .caption {
    padding-top: 20px;

    &__title {
      font-size: 26px;
      line-height: 30px;
    }
    &__subtitle {
      margin-bottom: 40px;
      font-family: 'Titan One';
      font-style: normal;
      font-weight: 400;
      font-size: 48px;
      line-height: 55px;
      text-align: center;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: $accent-color;
    }
  }

  .cow__block {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  .about__img-cow {
    margin-right: 32px;
  }

  .slogan {
    &__title {
      font-size: 14px;
      line-height: 176%;
    }

    &__text {
      font-size: 14px;
      line-height: 176%;
      letter-spacing: 0.04em;
    }
  }

  .about__btn--block {
    margin-top: 40px;
  }

  .about {
    margin-top: 80px;

    &__list {
      display: flex;
    }

    &__item {
      width: calc(30% - 20px);
    }

    &__icon {
      margin-bottom: 25px;
    }

    &__title {
      margin-bottom: 13px;
      font-family: 'Titan One';
      font-style: normal;
      font-weight: 400;
      font-size: 48px;
      line-height: 55px;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: $accent-color;
    }

    &__text {
      font-family: 'DM Sans';
      font-style: normal;
      font-weight: 700;
      color: $main-black-color;
      font-size: 14px;
      line-height: 174%;
    }
  }
}

// desctop
@media screen and (min-width: 1201px) {
  .caption {
    padding-top: 60px;

    &__title {
      font-size: 30px;
      line-height: 34px;
    }
    &__subtitle {
      margin-bottom: 40px;
      font-family: 'Titan One';
      font-style: normal;
      font-weight: 400;
      font-size: 58px;
      line-height: 66px;
      text-align: center;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: $accent-color;
    }
  }

  .cow__block {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }

  .about__img-cow {
    width: 500px;
  }

  .slogan {
    width: calc(40% - 0px);
    margin-top: 40px;

    &__title {
      font-size: 16px;
      line-height: 176%;
    }

    &__text {
      font-size: 14px;
      line-height: 194%;
      letter-spacing: 0.04em;
    }
  }

  .wrapper {
    position: relative;
    z-index: 1;
  }

  .about__btn--block {
    position: relative;

    margin-top: 40px;

    &::after {
      position: absolute;
      content: url(./../images/about/milk-desct.png);
      bottom: -100px;
      right: -100px;
    }
  }

  .about {
    padding: 120px 0px;

    &__list {
      display: flex;
      justify-content: space-between;
    }

    &__item {
      width: calc(25% - 20px);
    }

    &__icon {
      margin-bottom: 25px;
    }

    &__title {
      margin-bottom: 16px;
      font-family: 'Titan One';
      font-style: normal;
      font-weight: 400;
      font-size: 58px;
      line-height: 66px;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: $accent-color;
    }

    &__text {
      font-family: 'DM Sans';
      font-style: normal;
      font-weight: 700;
      color: $main-black-color;
      font-size: 16px;
      line-height: 176%;
    }
  }
}
