.contacts {
  
  background: linear-gradient(0deg, #fff5f6, rgba(0, 0, 0, 0) 75%);

  &__item {
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;
    padding: 40px 32px;
  }

  &__cafe,
  &__foodtruck {
    display: inline-block;
    text-transform: uppercase;
    color: $main-white-color;
    border-radius: 4px;
    margin-bottom: 32px;
    letter-spacing: 0.04em;
  }

  &__cafe,
  &__foodtruck {
    padding: 2px 20px;
    margin-bottom: 32px;
  }

  &__cafe {
    background-color: #f0d1a5;
  }

  &__foodtruck {
    background-color: #c2e297;
  }

  &__title {
    margin-bottom: 12px;
    letter-spacing: 0.04em;
  }

  &__text,
  &__contact-list {
    color: #907e82;
  }

  &__schedule-list {
    position: relative;
    margin: 64px 0;

    &::after {
      content: '';
      top: -25%;
      width: 100%;
      position: absolute;
      border-top: 1px solid #e1e1e1;
    }

    &::before {
      content: '';
      bottom: -25%;
      width: 100%;
      position: absolute;
      border-bottom: 1px solid #e1e1e1;
    }
  }

  &__schedule-time {
    display: block;
    color: #ffa5ba;
  }

  &__buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

@media screen and (max-width: 767px) {

  .contacts {
    position: relative;
    padding: 48px 0;
    font-size: 12px;

    &__list {
      margin-bottom: 32px;
    }

    &__item {
      max-width: 280px;
      margin: 0 auto;

      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }

    &__text {
      max-width: 180px;
    }

    &__schedule-list {
      position: relative;
      margin: 48px 0;

      &::after {
        content: '';
        top: -35%;
        width: 100%;
        position: absolute;
        border-top: 1px solid #e1e1e1;
      }

      &::before {
        content: '';
        bottom: -35%;
        width: 100%;
        position: absolute;
        border-bottom: 1px solid #e1e1e1;
      }
    }

    &__schedule-item {
      margin-bottom: 8px;
    }
  }

  .arrow-red::after,
  .arrow::after {
    top: 6px;
  }
}


@media screen and (min-width: 768px) and (max-width: 1199px) {

  .contacts {
    padding: 80px 0 40px;

    &__list {
      margin: 0 auto 40px;
      display: flex;
      justify-content: space-between;
      max-width: 800px;
    }

    &__item {
      max-width: 230px;
    }

    &__schedule-item,
    &__contact-item {
      margin-bottom: 8px;
    }
  }
}

@media screen and (min-width: 1200px) {

  .contacts {
    padding: 120px 0;

    &__list {
      display: flex;
      justify-content: space-between;
      margin-bottom: 60px;
    }

    &__item {
      max-width: 395px;
    }
  }
}
